.news-page {
    overflow: hidden;
    &__inner {
        @include xs {
            padding: 0; } }
    &__list {
        padding: 45px 0 70px;
        @include md {
            padding: 30px 0 40px; } }
    &__row {
        margin: -15px;
        @include md {
            margin: -8px; } }
    &__col {
        padding: 15px;
        @include md {
            padding: 8px; } } }
