.testimonial-block {
	padding-top: 150px;
	@include lg {
		padding-top: 78px; }
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__list {
		padding-top: 70px;
		@include sm {
			white-space: nowrap;
			overflow: hidden;
			opacity: 0;
			visibility: hidden;
			transition: opacity .3s, visibility .3s;
			&.slick-initialized {
				white-space: normal;
				overflow: visible;
				opacity: 1;
				visibility: visible; } }
		@include md {
			padding-top: 24px; } }
	.col-4 {
		@include md {
			margin-top: 16px; } }
	&__btn {
		@include md {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include md {
				display: block;
				text-align: center;
				margin-top: 25px; } } } }
