.course-block {
	padding-top: 155px;
	@include md {
		padding-top: 80px; }
	&.pilates-block {
		&:not(:first-child) {
			padding-top: 125px;
			@include md {
				padding-top: 80px; }
			.personal-office & {
				@include xs {
					padding-top: 52px; } } } }
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__title {
		.pilates-block & {
			@include sm {
				font-size: 25px; } }
		.personal-office & {
			margin-bottom: 20px;
			font-size: 44px;
			@include md {
				font-size: 25px; } } }
	&__list {
		padding-top: 65px;
		@include md {
			padding-top: 20px;
			// max-width: 610px
			margin: 0 auto; }
		.col-3 {
			@include md {
				margin-top: 16px; } }
		.pilates-block & {
			padding-top: 45px;
			@include md {
				padding-top: 30px; }
			.personal-office & {
				@include xs {
					padding-top: 0; } }
			.slick-list {
				@include xs {
					max-width: 400px; } } } }
	&__btn {
		@include sm {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include sm {
				display: block;
				text-align: center;
				margin-top: 34px; } } } }
