.footer {
	color: $white;
	padding: 73px 0;
	background-color: $footer;
	margin-top: 140px;
	@include sm {
		padding: 36px 0;
		margin-top: 84px; }
	a {
		border-bottom: 1px solid transparent;
		transition: border-color .3s;
		@include hover {
			border-color: currentColor; } }
	&__top {}
	&__list {}
	&__logo {
		max-width: 223px;
		@include sm {
			max-width: 127px; }
		a {
			border: none; } }
	&__socials {
		justify-content: flex-end;
		@include md {
			justify-content: flex-start;
			margin-top: 28px; } }
	&__bottom {
		// display: flex
		// justify-content: flex-end
		padding-top: 48px;
		font-size: 15px;
		&-inner {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap; }
		@include sm {
			padding-top: 34px; } }
	&__info {
		display: flex;
		padding-right: 20px;
		flex-wrap: wrap;
		@include ms {
			padding-right: 0; } }
	&__policy {
		@include ms {
			width: 100%; }
		&:not(:last-child) {
			margin-right: 30px;
			@include sm {
				margin-bottom: 13px; } } }
	&__studio {
		@include sm {
			margin-top: 40px; } } }


.socials {
	display: flex;
	flex-wrap: wrap;
	&__item {
		&:not(:last-child) {
			margin-right: 2em;
			@include sm {
				margin-right: 16px; } }
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 20px;
			border: 2px solid currentColor;
			width: 2.5em;
			height: 2.5em;
			border-radius: 10px;
			transition: border-color .3s, color .3s;
			@include hover {
				border-color: $blueHover;
				color: $blueHover; }
			@include sm {
				font-size: 16px;
				width: 35px;
				height: 35px; } } }
	.icon-vk {
		width: 1.3em; } }
